import React from 'react'
import 'bootstrap/dist/css/bootstrap.min.css'
const Card =(props)=>{
    console.log(props)
    return(
        <>
       <div className="card" style={{height:'600px',width:'400px'}}>
  <image className="card-img-top img-responsive" src={props.data.image} alt="Card image"   style={{width:'400px',height:'300px',backgroundPosition:'center',backgroundSize:'cover'}}/>
  <div className="card-body">
    <h4 className="card-title">{props.data.title}</h4>
    <p className="btn btn-info">Rs.{props.data.price}</p>
  </div>
</div>
</>
)
}
export default Card